.button {
  position: relative;
  padding: 15px 25px;
  color: white;
  background: #6c7a5d;
  transition: all 0.3s;
  font-size: 1.5em;
  line-height: 1.5em;
  cursor: pointer;
  border-radius: 5px;
  font-family: SiYuanHeiTi, Helvetica, Arial, sans-serif;

  &:hover {
    color: #6c7a5d;
    background: rgba(108, 122, 93, 0.5);
  }
}
