@keyframes pulse {
  0% {
    transform: scale(1, 1);
    border-color: black;
  }

  100% {
    transform: scale(1.2, 1.5);
    border-color: transparent;
  }
}

.about {
  padding: 48px 24px;
  height: 100%;
  width: 100%;
  position: relative;
  background-image: url('../../assets/svgs/about-bg.svg');
  background-position: center center;
  background-repeat: repeat;

  .background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
  }

  .goal {
    margin: 75px 0;
    text-align: center;
    line-height: 2;
  }

  .pulse {
    font-size: 2.5em;
    color: white;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      animation: pulse 2s infinite;
      border-radius: 4px;
      border: 3px solid rgba($color: #6c7a5d, $alpha: 0.5);
    }
  }
}
